/* ----- header component start ---------*/
/* -------------------------------------- */
.header {
    height: 60px;
    width: 100%;
    background: var(--theme-color);
    display: grid;
    grid-template-columns: 200px 1fr;
    align-items: center;
    color: #fff;
}

.header_right {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.logo {
    height: 40px;
    object-fit: contain;
    padding-left: 25px;
}

.admin_img {
    width: 35px;
    height: 35px;
    object-fit: cover;
    border-radius: 50%;
}

.header_right .left {
    display: flex;
    align-items: center;
    gap: 20px;
    padding-left: 30px;
}

.header_right .profile {
    display: flex;
    gap: 20px;
    padding-right: 25px;
    align-items: center;
}

.header_right .profile h4 {
    font-size: 1rem;
}

.header_right .profile h5 {
    font-size: 0.7rem;
}

.header_right .left h1 {
    text-transform: capitalize;
    font-size: 1.7rem;
}

.humberger_menu {
    font-size: 25px;
    cursor: pointer;
    display: none;
}

/* -------------------------------------- */
/* ----- header component end -----------*/


/* ----- main_layout component start ---------*/
/* -------------------------------------- */
.main_layout {
    display: grid;
    grid-template-columns: 200px 1fr;
    background: rgb(40 72 135 / 13%);
}

.main_layout .left {
    background: #fff;
    box-shadow: rgb(0 0 0 / 24%) 0px 3px 8px;
}

.main_layout .right {
    padding: 30px;
    min-height: 100vh;
}

/* -------------------------------------- */
/* ----- main_layout component end -----------*/


/* ----- sidebar component start ---------*/
/* -------------------------------------- */
.sidebar {
    width: 100%;
    box-shadow: rgb(0 0 0 / 24%) 0px 3px 8px;
    height: 100%;
}

.sidebar ul li a {
    display: flex;
    align-items: center;
    gap: 10px;
    padding: 15px 25px;
    font-weight: 500;
    font-size: 17px;
    color: #585858;
}

.sidebar ul li button {
    display: flex;
    align-items: center;
    gap: 10px;
    padding: 15px 25px;
    font-weight: 500;
    font-size: 17px;
    color: #585858;
    width: 100%;
    background: transparent;
    border: 0;
}

.sidebar ul li :hover {
    background: rgba(40, 72, 135, 0.13);
    color: var(--theme-color);
}

.sidebar_logo {
    display: none;
}

.hide-nav {
    left: -200px;
}


.sidebar_closeBtn {
    color: #fff;
    border: 0px;
    background: transparent;
    font-size: 25px;
}

/* -------------------------------------- */
/* ----- sidebar component end -----------*/



/* ----- home page start ---------*/
/* -------------------------------------- */
.home_topBar {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.home_topBar h1 {
    font-size: 32px;
    font-weight: 700;
}

.search_input {
    border: 0;
    width: 30%;
    background: #fff;
    border-bottom: 2px solid #1976d2;
    border-radius: 4px;
    padding: 10px 20px;
    font-weight: 500;
    font-size: 16px;
    box-sizing: border-box;
}

.dashboard_cardMain {
    display: flex;
    margin-top: 50px;
    flex-wrap: wrap;
    gap: 30px;
    justify-content: center;
}

.dashboard_cardMain .dashboard_card {
    background: #fff;
    padding: 24px;
    background: #FFFFFF;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.21);
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 249px;
    cursor: pointer;
}

.dashboard_cardMain .dashboard_card:hover {
    background: var(--theme-color);
    color: #fff;
}


.dashboard_cardMain .icon {
    font-size: 50px;
    color: var(--theme-color);
}

.dashboard_cardMain .dashboard_card:hover .icon {
    color: #fff;
}

.dashboard_cardMain p {
    font-family: 'Raleway';
    font-style: normal;
    font-weight: 600;
    font-size: 12px;
    text-transform: capitalize;
}

.dashboard_cardMain h1 {
    font-family: 'Inter';
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    margin-top: 10px;
}

/* -------------------------------------- */
/* ----- home page end -----------*/


/* ----- Territories page end -----------*/
/* -------------------------------------- */
.__filter {
    margin-top: 20px;
    display: flex;
    gap: 30px;
    border-bottom: 1px solid #1976d2;
}

.__filter p {
    padding-bottom: 10px;
    position: relative;
    cursor: pointer;
    font-weight: 500;
    /* margin-bottom: 10px; */
}

.__filter p:hover {
    color: #1976d2;
}

.__filter p:hover::after {
    position: absolute;
    content: "";
    width: 100%;
    height: 2px;
    left: 0;
    bottom: 0px;
    background: var(--theme-color);
}

.active__filter {
    color: var(--theme-color);
    font-weight: 600 !important;
}

.active__filter::after {
    display: block;
    position: absolute;
    content: "";
    width: 100%;
    height: 2px;
    left: 0;
    bottom: 0px;
    background: var(--theme-color);
}

.inactive__filter {
    color: #222222;
}

.inactive__filter::after {
    display: none;
}


/* -------------------------------------- */
/* ----- Territories page end -----------*/


/* ----- Table component start -----------*/
/* -------------------------------------- */
.table_div {
    overflow: auto;
}

.table_div table {
    width: 100%;
}

.table_div table th {
    font-size: 16px;
    font-weight: 400;
    color: var(--theme-color);
    text-align: start;
    background: #fff;
    padding: 0.5rem;
    font-weight: 600;
    /* cursor: pointer; */
}

.table_div table td {
    font-size: 16px;
    font-weight: 400;
    color: #222222;
    text-align: start;
    border-bottom: 1px solid #1976d23b;
    padding: 0.5rem;
}

.table_div table tr:hover {
    background: rgba(255, 255, 255, 0.521);
}

/* -------------------------------------- */
/* ----- Table component end -----------*/


/* ----- Pagination component end -----------*/
/* -------------------------------------- */
.pagination-grid-main {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.paginationBtns {
    display: flex;
    list-style-type: none;
    align-items: center;
}

.pagination-icon {
    font-size: 28px;
}

.paginationBtns li {
    margin-right: 10px;
}

.paginationBtns li:first-child {
    margin-left: 0px;
}


.paginationBtns li:last-child {
    margin-right: 0px;
}

.paginationBtns a {
    display: inline-block;
    border: 1px solid var(--theme-color);
    box-sizing: border-box;
    border-radius: 10px;
    padding: 5px 8px;
    cursor: pointer;
    color: var(--theme-color);
    font-weight: 500;
    font-size: 18px;
    line-height: 100%;
    background: #fff;
}

/* .paginationBtns a:hover{
    background-color: var(--theme-color) !important;
    color: #fff !important;
} */

.previousBtn, .nextBtn {
    font-weight: 500;
    font-size: 16px;
    line-height: 100%;
    color: var(--color-grey);
    cursor: pointer;
    background-color: transparent !important;
    border: 0px solid red !important;
}

.previousBtn:hover {
    color: #3770C9;
}

.nextBtn:hover {
    color: #3770C9;
}

.paginationActive a {
    background-color: var(--theme-color) !important;
    border: 1px solid var(--theme-color) !important;
    color: #fff !important;
}

.paginationDisable {
    opacity: 0.5;
}

.pagination_left {
    color: #222222;
    font-size: 14px;
}

.pagination_left select {
    border: 0;
    background: var(--theme-color);
    color: #fff;
}


/* -------------------------------------- */
/* ----- Pagination component end -----------*/