.missingMain img {
    max-width: 400px;
}

.missingMain .dataNotFoundImg {
    max-width: 200px !important;
}

.missingMain h1 {
    font-size: 2rem;
    font-weight: 600;
    text-align: center;
    margin-top: 16px !important;
    color: var(--main);
}

@media only screen and (max-width: 600px) {
    .missingMain {
        margin: 0px 30px;
    }

    .missingMain img {
        max-width: 100%;
    }

    .missingMain h1 {
        font-size: 1rem;
    }
}