.login_body {
  height: 100vh;
  width: 100%;
  /* background: #fff; */
  display: grid;
  place-items: center;
}

.login_logo {
  text-align: center;
  display: block;
  margin: 30px 0px;
}

.login_container {
  width: 350px;
  height: auto;
  padding: 30px 50px;
  background: rgba(40, 72, 135, 0.08);
  border-radius: 10px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.login_container h2 {
  font-size: 24px;
  text-align: center;
}

.input_container {
  position: relative;
}

.input_container .icon_btn {
  position: absolute;
  top: 50%;
  right: 30px;
  transform: translateY(-50%);
  color: rgb(40, 72, 135);
  border: 0px;
  cursor: pointer;
  font-size: 18px;
  background: transparent;
}

.mt_20 {
  margin-top: 20px;
}

.mb_20 {
  margin-bottom: 20px;
}

.input {
  border: 0px;
  padding: 10px 20px;
  background: transparent;
  border-bottom: 1px solid rgba(40, 72, 135, 0.25);
  width: 100%;
  box-sizing: border-box;
  font-size: 16px;
}

.input::placeholder {
  color: rgba(88, 88, 88, 0.64);
}

.input:focus {
  border-bottom: 1px solid rgba(40, 72, 135, 0.637);
}

.input_select {
  -webkit-appearance: none;
  -moz-appearance: none;
  background: url(../images/arrow_down.png);
  background-repeat: no-repeat;
  background-position-x: 94%;
  background-position-y: 13px;
  border-radius: 2px;
  margin-right: 2rem;
  padding: 1rem;
  padding-right: 2rem;
}

.forgot_text {
  display: block;
  font-size: 12px;
  color: rgb(40, 72, 135);
  text-align: end;
  margin-top: 10px;
  margin-bottom: 20px;
}

.login_btn {
  background: rgb(40, 72, 135);
  border: 0px;
  padding: 10px;
  width: 100%;
  border-radius: 30px;
  color: #fff;
  font-size: 16px;
  cursor: pointer;
}

.social_login {
  margin-top: 50px;
  display: flex;
  gap: 30px;
  align-items: center;
}

.social_icon {
  display: flex;
  gap: 10px;
}

.social_icon div {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  display: grid;
  place-items: center;
  color: #fff;
  cursor: pointer;
  font-size: 20px;
}

.social_icon div:nth-child(1) {
  background: #4867aa;
}

.social_icon div:nth-child(2) {
  background: #2abdef;
}

.social_icon div:nth-child(3) {
  background: #db4437;
}

@media only screen and (max-width: 600px) {
  .login_container {
    width: 300px;
  }

  .social_icon div {
    width: 30px;
    height: 30px;
    font-size: 12px;
  }
}

@media only screen and (max-width: 450px) {
  .login_container {
    width: auto;
    max-width: 100%;
    margin: 20px;
  }

  .login_logo {
    height: 25px;
    margin: 25px 0px;
  }

  .social_icon div {
    width: 30px;
    height: 30px;
    font-size: 12px;
  }

  .login_container {
    padding: 30px;
  }
}

/* float circle  */
.circle_main {
  position: absolute;
  left: -10vh;
  top: -10vh;
  z-index: -5;
}

.circle {
  border-radius: 50%;
  position: absolute;
  left: -20vh;
  top: -20vh;
  z-index: -5;
  animation-name: circle;
  animation-duration: 10s;
  animation-iteration-count: infinite;
}

@keyframes circle {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.25);
  }

  100% {
    transform: scale(1);
  }
}

.circle_1 {
  width: 40vh;
  height: 40vh;
  background: rgb(40, 72, 135);
  z-index: -1;
}

.circle_2 {
  width: 50vh;
  height: 50vh;
  background: rgba(40, 72, 135, 0.781);
  z-index: -2;
}

.circle_3 {
  width: 60vh;
  height: 60vh;
  background: rgba(40, 72, 135, 0.555);
  z-index: -3;
}

.circle_4 {
  width: 80vh;
  height: 80vh;
  background: rgba(40, 72, 135, 0.315);
  z-index: -4;
}

@media only screen and (max-width: 600px) {
  .circle_1 {
    width: 30vh;
    height: 30vh;
  }
  .circle_2 {
    width: 35vh;
    height: 39vh;
  }
  .circle_3 {
    width: 40vh;
    height: 44vh;
  }
  .circle_4 {
    width: 47vh;
    height: 50vh;
  }
}

/* float circle  */

.forgot_info {
  font-size: 12px;
  color: grey;
  margin-top: 12px !important;
}

.back_link {
  display: flex;
  align-items: center;
  gap: 4px;
  color: var(--theme-color);
  margin-top: 30px;
}