@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;300;400;500;600;700;800;900&family=Raleway:wght@100;200;300;400;500;600;700;800;900&display=swap');

* {
  margin: 0;
  padding: 0;
  outline: 0;
  font-family: 'Inter', sans-serif;
}

:root {
  --theme-color: #1976d2;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin-top: 0 !important;
  margin-bottom: 0 !important;
  font-weight: normal;
}

a {
  text-decoration: none !important;
}

ul,
ol {
  list-style: none;
  padding-left: 0rem !important;
}

select {
  cursor: pointer;
}

button {
  cursor: pointer;
}

.w-100 {
  width: 100%;
}

/* toastify color start */
.Toastify__progress-bar--success {
  background: var(--theme-color);
}

/* toastify color end */

/* mui*/
.Table_icon_btn {
  padding: 0 !important;
}

.Table_icon_btn.delete {
  padding: 0 !important;
  color: #d32f2f;
}

/* mui*/

.c-pointer {
  cursor: pointer;
}

.input_one_container label {
  color: var(--theme-color);
  margin: 10px 0px;
  text-transform: capitalize;
}

.input_one {
  border: 0px;
  padding: 10px 20px;
  border: 1px solid rgba(40, 72, 135, 0.33);
  border-radius: 8px;
  background: #fff;
  width: 100%;
  box-sizing: border-box;
  font-size: 16px;
}

.modal-content {
  background: #d9d9d9 !important;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25) !important;
  border-radius: 8px !important;
}

/*------------ button start --------------*/
/* ----------------------------------------------- */
.__btn {
  padding: 10px 25px;
  font-size: 16px;
  border: 0px;
  border-radius: 8px;
}

.btn_blue {
  background: var(--theme-color);
  color: #fff;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  text-transform: capitalize;
}

.btn_green {
  background: green;
  color: #fff;
}

.btn_red {
  background: rgba(255, 0, 0, 0.801);
  color: #fff;
}

/*  */

/*------------ button end --------------*/
/* ----------------------------------------------- */

.prevent-select {
  -webkit-user-select: none;
  /* Safari */
  -ms-user-select: none;
  /* IE 10 and IE 11 */
  user-select: none;
  /* Standard syntax */
}

.v-top {
  vertical-align: top;
}

/* ReactSimpleImageViewer */
#ReactSimpleImageViewer .react-simple-image-viewer__close {
  opacity: 1 !important;
  color: rgba(255, 0, 0, 0.938) !important;
}

#ReactSimpleImageViewer span {
  opacity: 1 !important;
  color: var(--theme-color) !important;
}

/* ReactSimpleImageViewer */

/* create a custom scroll bar start */

::-webkit-scrollbar {
  width: 10px;
}


::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey;
  border-radius: 10px;
}


::-webkit-scrollbar-thumb {
  background: rgba(0, 0, 0, 0.575);
  border-radius: 10px;
}


::-webkit-scrollbar-thumb:hover {
  background: rgba(0, 0, 0, 0.671);
}

/* create a custom scroll bar end */